.Game {
  overflow: hidden;
}

.Settings__stage {
  position: absolute;
  top: 80px;
  bottom: 70px;
  left: 25%;
  right: 25%;
  overflow: auto;
  text-align: center;
}

.Settings__title {
  font-size: 30px;
  font-weight: bold;
  margin: 20px 0;
}

.Settings__row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.Settings__label,
.Settings__field {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: left;
  margin: 0 10px;
}

.Settings__input {
  width: 100%;
}

.Settings__center {
  text-align: center;
}

.Settings__button {
  background-color: black;
  border: none;
  color: white;
  padding: 10px;
  margin-top: 20px;
}

.Settings__text {
  padding: 10px;
  margin-top: 20px;
}

.Settings__icon {
  height: 40px;
}

@media (max-width: 650px) {
  .Settings__stage {
    left: 0;
    right: 0;
  }
}



