.People {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 50px;
  overflow: hidden;
}

.People__animation {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: -100%;
}

.People__animation.enter {
  left: 0;
}

.People__animation.exit {
  left: 100%;
}

.People__asset-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.People__asset {
  max-width: 100%;
  max-height: 100%;
}



