.Header {
  position: absolute;
  top: 0;
  height: 80px;
  left: 0;
  right: 0;
  padding: 10px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Header__prev {
  font-size: 30px;
  color: inherit;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.Header__prev:hover {
  color: inherit;
  text-decoration: underline;
}

.Header__title {
  font-size: 60px;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
}

.Header__score {
  font-size: 30px;
}

@media (max-width: 650px) {
  .Header {
    padding: 10px;
  }

  .Header__title {
    font-size: 30px;
  }

  .Header__prev,
  .Header__score {
    font-size: 20px;
  }
}



